
import menu from '../assets/menu3.png';
import loader from '../assets/loader.gif';
import '../App.css';
import { motion } from "framer-motion"
import { db } from "../config";
import { getDocs, collection } from "firebase/firestore";
import {useEffect, useState} from 'react'

function Home() {
    

    var url = window.location.href;
    const qr = url.substring(url.lastIndexOf('=') + 1);


    console.log(qr);

    const [user,setUser] = useState([]);

    useEffect(()=> {
        console.log('...........')

        const getusers = async () => {
            const user_collection_ref = collection(db, "users")
            const data = await getDocs(user_collection_ref);
            console.log(data)

            const localUsers = data.docs.map((doc) => ({ ...doc.data(), id: doc.id }))
            
            console.log(localUsers)

            const localUser = localUsers.filter(user => {
                return user.id === qr
            
            })

            setUser(localUser[0]);
            console.log("user : ")
            console.log(localUser.name)
        }

      
        getusers()

    } , [])


    return(
        <div className="App">
            <motion.div
                className="preloader-container"
                initial={{ zIndex: 5, opacity: 1 }}
                animate={{ opacity: 0, transition: { delay: 2, duration: 1, ease: "easeInOut" } }}
            >
                <img src={loader} alt="" />
            </motion.div>
            <div className="back-img">
                <div className="black-overlay">
                    <p>welcome again</p>
                </div>
            </div>
            <div className="header">
                <div className="logo-container">
                    <p>Mr. Vape</p>
                </div>

                <div className="menu-toggle">
                    <img src={menu} alt="" />
                </div>


            </div>

            <motion.div
                className="profile-container"
                initial={{ scale: 20 }}
                animate={{ scale: 1, translateX: '-50%', translateY: '-50%', transition: { delay: 2.5, ease: 'easeInOut', duration: 1 } }}
            >

            </motion.div>

            <motion.div
                className="profile-infos"
                initial={{ scale: 0.5, opacity: 0 }}
                animate={{ scale: 1, opacity: 1, transition: { delay: 3, ease: 'easeInOut', } }}

            >
                <div className="name-container">
                    <p>{user != null ? user.name : "no corresponding client"}</p>
                    <p>Mr. Vapo client</p>
                    <p>{qr}</p>
                </div>

                <div className="point-container">
                    <p>Points</p>
                    <div className="v-line"></div>
                    <p>{user != null ? user.points : ""} (P)</p>
                </div>

                <div className="action-container">
                    <p>Buy more, earn more</p>
                </div>

                <p>Vapo shop</p>

            </motion.div>
        </div>

    )
}
export default Home;